import { css } from 'lit';

export const styles = css`:host{--stroke-thickness: 1.3px;--circular-size: calc(var(--diameter) - var(--stroke-thickness));--scale-factor: 2.95}[part~=label],[part~=value]{font-size:round(var(--diameter)/var(--scale-factor) - var(--stroke-thickness),1px)}[part~=label]{color:var(--text-color)}[part~=track]{stroke:var(--base-circle-color)}[part~=gradient_start],[part~=gradient_end]{stop-color:var(--progress-circle-color)}:host([variant=danger]) [part~=gradient_start],:host([variant=danger]) [part~=gradient_end]{stop-color:hsla(var(--ig-error-500), var(--ig-error-a))}:host([variant=warning]) [part~=gradient_start],:host([variant=warning]) [part~=gradient_end]{stop-color:hsla(var(--ig-warn-500), var(--ig-warn-a))}:host([variant=info]) [part~=gradient_start],:host([variant=info]) [part~=gradient_end]{stop-color:hsla(var(--ig-info-500), var(--ig-info-a))}:host([variant=success]) [part~=gradient_start],:host([variant=success]) [part~=gradient_end]{stop-color:hsla(var(--ig-success-500), var(--ig-success-a))}`;
